var speakerNames=["John Sinclair","Kristi Straus","Fauzia Lala","Izdihar Bailey","Jeanne Suchodolski","Heather Evans","Ansel Santosa","Jeffrey Lew","Tyler Valentine","Dr. Steve Bezruchka","Tarah Wheeler","Karen Liftin","Andrea Weatherhead","Sarah Myhre"];var speakers=[{"name":"John Sinclair","start":"May 5, 2018 10:10:00","end":"May 5, 2018 10:23:00","talk":"The Mud that Makes You","description":"John Sinclair (also known as Saint Claire) is a rising songwriter, producer, and performer in Seattle.  His work in singing, writing, producing, and composing for strings has led to recent music collaborations with prominent musicians, including Macklemore and Ryan Lewis.","shift":"-40vw","img":"/resources/john.png"},{"name":"Kristi Straus","start":"May 5, 2018 10:23:00","end":"May 5, 2018 10:36:00","talk":"Sustainable Happiness","description":"Dr. Kristi Straus is a lecturer at the University of Washington Program on the Environment and the recipient of the 2017 University of Washington Distinguished Teaching Award. She is passionate about environmental conservation as well as science and sustainability education.","shift":"-26vw","img":"/resources/kristi.png"},{"name":"Fauzia Lala","start":"May 5, 2018 10:36:00","end":"May 5, 2018 10:49:00","talk":"Practice Failure for Breakthroughs","description":"Fauzia Lala is an ex-Microsoft engineer, motivational speaker and a serial entrepreneur. She has 3 startups including self-defense training for women in the Greater Seattle area. Fauzia loves to learn and read about all things psychology and personal/spiritual growth. She was a finalist of Forbes 30 Under 30 in 2016 and one of the winners of the Washington InnovateHer competition in 2017.","shift":"-40vw","img":"/resources/fauzia.png"},{"name":"Aura","start":"May 5, 2018 10:49:00","end":"May 5, 2018 11:01:00","talk":"","description":"","shift":"-28vw","img":"/resources/aura.png"},{"name":"Izdihar Bailey","start":"May 5, 2018 11:01:00","end":"May 5, 2018 11:14:00","talk":" Liberal Islamophobia","description":"Izdihar Bailey is a Lobbyist and a former Government Affairs Team member at CAIR-Washington State, America's largest Muslim civil liberties and advocacy organization. As someone who has lived life without Islam and is now empowered by Islam, she believes her life experience allows her to create stronger link of communication with fellow Americans of all faiths and political alignments.","shift":"-42vw","img":"/resources/izdihar.png"},{"name":"Jeanne Suchodolski","start":"May 5, 2018 11:14:00","end":"May 5, 2018 11:27:00","talk":"Leading in the Dark","description":"Jeanne C. Suchodolski, Explorer's Club MN '09, is a multifaceted powerhouse of a woman with titles such as engineer, lawyer, pilot, and polar expedition member. She has remained within the technology and aerospace world throughout her career, from working as a rocket engineer at Boeing to acting as patent and corporate counsel for companies such as Boeing, Microsoft, and Honeywell. Jeanne is a multi-year veteran of the United States Antarctic Program and wintered-over as the Station Manager of  McMurdo Station. She is currently earning an LLM in Space, Cyber and Telecommunications Law.","shift":"-19.4vw","img":"/resources/jeanne.png"},{"name":"Heather Evans","start":"May 5, 2018 12:27:00","end":"May 5, 2018 12:40:00","talk":"Uncovering: an Empowering Pathway to Disability Identity","description":"Heather is a lecturer at the University of Washington, teaching several courses pertaining to disability studies. Her work revolves around better understanding invisible disability, empowering disability identity, and breaking down institutional marginalization of disabled individuals.","shift":"-28vw","img":"/resources/heather.png"},{"name":"Human Condition","start":"May 5, 2018 12:40:00","end":"May 5, 2018 12:52:00","talk":"","description":"","shift":"-28vw","img":"/resources/gaultier.png"},{"name":"Ansel Santosa","start":"May 5, 2018 12:52:00","end":"May 5, 2018 13:05:00","talk":"A new model for building companies","description":"Ansel is an engineer and educator. He graduated from the University of Washington Information School in 2013 where he now serves as an advisory board member. He works as a software engineer and early business validator at Pioneer Square Labs, a local startup studio and seed stage venture fund. He is passionate about helping entrepreneurs be successful and building great companies in Seattle.","shift":"-33.3vw","img":"/resources/ansel.png"},{"name":"Jeffrey Lew","start":"May 5, 2018 13:05:00","end":"May 5, 2018 13:18:00","talk":"Ending the Shame: Erasing School Lunch Debt for Good","description":"Jeffery Lew was born and raised in Seattle and graduated from the University of Washington with a BA in marketing. As a proud graduate of Seattle Public Schools (Ingraham High) and a father of three, the problem of school lunch debt and lunch shaming hit close to home for him. Jeff could not fathom students being shamed or going hungry for having lunch debt at their school cafeteria, a situation that children have no control over. In 2017, Jeff began a movement to end lunch shaming and find a permanent solution to the problem. Also known as the 'Lunch Dad,' Jeff launched a campaign to pay off all the lunch debt in Seattle, Tacoma and Spokane, the three largest school districts in the state of Washington. He has raised over $100,000 to date and is now aiming to pay off the lunch debt for every district in the state. Still, not everyone is aware of school lunch debt and the shaming that stems from it. Jeff is on a mission to raise awareness and find a way to end this practice once and for all.","shift":"-42vw","img":"/resources/jeffery.png"},{"name":"Tyler Valentine","start":"May 5, 2018 13:28:00","end":"May 5, 2018 13:41:00","talk":"Fighting for a Place in Space","description":"Tyler is studying Earth and Space Sciences at University of Washington, with minors in Philosophy and Physics. He researches extraterrestrial water extraction with the Applied Physics Laboratory. He also values public engagement about science issues, speaking at events like the Seattle March for Science.","shift":"-47.22vw","img":"/resources/tyler.png"},{"name":"UW Awaaz","start":"May 5, 2018 14:26:00","end":"May 5, 2018 14:38:00","talk":"","description":"","shift":"-28vw","img":"/resources/awaaz.png"},{"name":"Dr. Steve Bezruchka","start":"May 5, 2018 14:38:00","end":"May 5, 2018 14:51:00","talk":"Inequality in early life is bad for US health","description":"Dr. Stephen Bezruchka is a Senior Lecturer in the Departments of Health Services and Global Health, with degrees from Harvard, Stanford and Johns Hopkins. His courses and research focus on population health and their socioeconomic determinants. Prior to his time at the University of Washington, he spent 30 years as an emergency physician and trained doctors in remote hospitals in Nepal. Throughout his career, he has continued to be involved with international health issues.","shift":"-37vw","img":"/resources/bezruchka.jpg"},{"name":"Tarah Wheeler","start":"May 5, 2018 14:51:00","end":"May 5, 2018 15:04:00","talk":"Cybersecurity Is Happening To You","description":'<a href="http://twitter.com/tarah">Tarah Wheeler</a> (MS, CSM, CSD, CSP, CISSP) is an information security researcher, author, entrepreneur, keynote speaker, nonprofit founder, scientist, and poker player. She is currently <a href="https://www.newamerica.org/cybersecurity-initiative/2017-2018-cybersecurity-policy-fellows/">Cybersecurity Policy Fellow at New America</a> and Principal Security Advisor & CEO at <a href="https://redqueentech.com/">Red Queen Technologies.</a> She is the lead author of the best-selling <a href="http://amzn.to/2q9iFik"><em>Women In Tech: Take Your Career to The Next Level With Practical Advice And Inspiring Stories</em></a>. She has been Senior Director of Engineering and Principal Security Advocate at <a href="http://symantec.com/">Symantec</a> Website Security. She founded and now sits on the board of Fizzmint, an end-to-end secure HR management company. She has led projects at <a href="http://www.xbox.com/en-US/games/xbox-one">Microsoft Game Studios</a> (Halo and Lips) and architected systems at encrypted mobile communications firm <a href="http://silentcircle.com/">Silent Circle</a>. She founded <a="http://isunlocked.com/">Infosec Unlocked</a>, an initiative to add diversity in infosec conference speakers). Reach her at <a href="http://twitter.com/tarah">@tarah</a>.',"shift":"-40vw","img":"/resources/tarah.png"},{"name":"Karen Liftin","start":"May 5, 2018 15:14:00","end":"May 5, 2018 15:27:00","talk":"Becoming Planetary: Reinventing Politics for the Anthropocene","description":"Karen Litfin is Associate Professor of Political Science at the University of Washington, and author of <em>Ozone Discourses</em>; <em>The Greening Sovereignty</em>; and <em>Ecovillages: Lessons for Sustainable Community</em>.","shift":"-33vw","img":"/resources/karen.png"},{"name":"Andrea Weatherhead","start":"May 5, 2018 15:27:00","end":"May 5, 2018 15:40:00","talk":"Only Connect","description":'Andrea has created award winning immersive environments for many museums across the country including the Experience Music Project (now called MoPOP) and the  Museum of History & Industry in Seattle. Her visionary educational and interactive exhibit environments have earned her numerous awards. Andrea was featured as the woman to watch out for in technology in the 21st century by Seattle Magazine and  was cited as one of Seattles 36 most influential people by Seattle Met Magazine.',"shift":"-39vw","img":"/resources/andrea.png"},{"name":"Sarah Myhre","start":"May 5, 2018 15:40:00","end":"May 5, 2018 15:53:00","talk":"Take Your Humanity With You","description":"Dr. Myhre is a paleoceanographer and science communicator, currently researching in the UW Department of Oceanography. She organized the Seattle chapter of 500 Women Scientists and has been recognized as an active voice for women in science and society.","shift":"-28vw","img":"/resources/sarah.png"}];/* harmony default export */ __webpack_exports__["a"] = (speakers);

